import { Stack } from "@mui/material";
import { Fragment } from "react";
import artifactAssets from "../data/Artifacts";
import weaponAssets from "../data/Weapons";
import { ArtifactSetKey, WeaponKey } from "../types/consts";
import BootstrapTooltip from "./BootstrapTooltip";

const defaultWeaponSpan = ({
  children,
  code = "AmenomaKageuchi",
}: ContainerProps) => {
  return (
    <BootstrapTooltip
      sx={{ borderRadius: 4 }}
      title={
        <Stack flexDirection="row" spacing={2}>
          <img
            src={weaponAssets[(code as WeaponKey) ?? "AmenomaKageuchi"].Icon}
            style={{ width: "64px", height: "64px" }}
            alt={code}
          />
        </Stack>
      }
      disableInteractive
      arrow
      placement="right"
    >
      <span>{children}</span>
    </BootstrapTooltip>
  );
};

const defaultArtifactSetSpan = ({
  children,
  code = "Adventurer",
}: ContainerProps) => {
  return (
    <BootstrapTooltip
      sx={{ borderRadius: 4 }}
      title={
        <Stack flexDirection="row" spacing={2}>
          <img
            src={
              artifactAssets[(code as ArtifactSetKey) ?? "Adventurer"].Flower
            }
            style={{ width: "64px", height: "64px" }}
            alt={code}
          />
        </Stack>
      }
      disableInteractive
      arrow
      placement="right"
    >
      <span>{children}</span>
    </BootstrapTooltip>
  );
};

const defaultFullParent = ({ children }: ContainerProps) => {
  return <span style={{ whiteSpace: "pre-wrap" }}>{children}</span>;
};
const defaultSpanParent = ({ children, code = "" }: ContainerProps) => {
  return <span title={code}>{children}</span>;
};
interface StringRangesProps {
  str: string;
  indices: SE[];
  fullParent?: Container;
  spanParent?: Container;
}
const StringRanges = ({
  str,
  indices,
  fullParent = defaultFullParent,
  spanParent = defaultSpanParent,
}: StringRangesProps) => {
  if (indices.length === 0) return fullParent({ children: str });
  const ind = indices.sort((a, b) => {
    if (a.start < b.start) return -1;
    if (a.start > b.start) return 1;
    return 0;
  });
  const finalChildren = ind.map((indice: SE, index: number) => {
    const isFirstIndice = index === 0;
    const preText = isFirstIndice
      ? str.substring(0, indice.start) ?? ""
      : str.substring(ind[index - 1].end, indice.start) ?? "";
    const isLastIndice = index === ind.length - 1;
    return (
      <Fragment key={index}>
        {preText}
        {spanParent({
          children: str.substring(indice.start, indice.end),
          code: indice.code,
        })}
        {isLastIndice && <span>{str.substring(indice.end, str.length)}</span>}
      </Fragment>
    );
  });
  return fullParent({ children: finalChildren });
};
export default StringRanges;
export { defaultWeaponSpan, defaultArtifactSetSpan };
