import { Navigate, useNavigate, useParams } from "react-router-dom";
import { allCharacterKeys, CharacterKey, ElementKey } from "../types/consts";
// import allCharacterInfo from "../data/Characters/allCharacterInfo.json";
import allCharacterBuilds from "../data/characterBuilds.json";
import { allCharInfo as allCharacterInfo } from "../types/api";
import {
  AccordionDetails,
  Box,
  Card,
  Grid,
  MenuItem,
  Skeleton,
  Tab,
  Tabs,
} from "@mui/material";
import { Suspense, useCallback, useEffect, useState } from "react";
// import characterAssets from "../data/Characters";
import CardContentEvenPadding from "../components/CardContentEvenPadding";
import CloseButton from "../components/CloseButton";
import CharSelectDropdown from "../components/CharSelectDropdown";
import useLocalStorage from "../hooks/useLocalStorage";
import { allTravelerElements, TravelerElementKey } from "../types/traveler";
import DropdownButton from "../components/DropdownButton";
import { theme } from "../Theme";
import BuildDisplay from "../components/BuildDisplay";
import characterAssets from "../data/Characters";
import CAccordion, { CAccordionSummary } from "../components/CAccordion";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

const TabPanel = (props: TabPanelProps) => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && children}
    </div>
  );
};

const a11yProps = (index: number) => {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
};

interface PageCharacterProps {
  headerHeight: number;
}

const PageCharacter = ({ headerHeight }: PageCharacterProps) => {
  const navigate = useNavigate();
  const onClose = useCallback(() => navigate("/"), [navigate]);

  const { characterKey } = useParams<{ characterKey?: CharacterKey }>();
  const invalidKey = !allCharacterKeys.includes((characterKey as any) ?? "");
  if (invalidKey) return <Navigate to="/" />;
  return (
    <Box my={1} display="flex" flexDirection="column" gap={1}>
      <Suspense
        fallback={<Skeleton variant="rectangular" width="100%" height={1000} />}
      >
        {characterKey && (
          <CharacterDisplayCard
            characterKey={characterKey}
            onClose={onClose}
            headerHeight={headerHeight}
          />
        )}
      </Suspense>
    </Box>
  );
};

interface CharacterDisplayCardProps extends PageCharacterProps {
  characterKey: CharacterKey;
  onClose?: () => void;
}

const CharacterDisplayCard = ({
  characterKey,
  onClose,
  headerHeight,
}: CharacterDisplayCardProps) => {
  const [buildTab, setBuildTab] = useState(0);
  const handleBuildChange = (_e: React.SyntheticEvent, newValue: number) => {
    setBuildTab(newValue);
  };
  const [travelerElement, setTravelerElement] =
    useLocalStorage<TravelerElementKey>("travelerElement", "anemo");
  const travelerId = `traveler-${travelerElement}`;
  const isTraveler = characterKey === "Traveler";

  const characterInfo = allCharacterInfo.find((char) =>
    isTraveler ? char.id === travelerId : char.characterKey === characterKey
  ) as any;
  const characterBuilds = allCharacterBuilds.find((character) => {
    return (
      (isTraveler ? character.element === travelerElement : true) &&
      character.name === characterKey
    );
  });
  useEffect(() => {
    setBuildTab(0);
  }, [characterKey, travelerElement]);
  console.log(characterBuilds?.builds[buildTab]);

  return (
    <>
      <Card
        sx={{
          borderRadius: 4,
          position: "sticky",
          top: `${headerHeight + 8}px`,
          zIndex: 2,
        }}
      >
        <CardContentEvenPadding
          sx={{ display: "flex", flexDirection: "column", gap: 1 }}
        >
          <Box display="flex">
            <Box display="flex" gap={1} flexWrap="wrap" flexGrow={1}>
              <CharSelectDropdown
                characterKey={characterKey}
                characterName={characterInfo.name}
                color={
                  (characterInfo.vision_key.toLowerCase() as ElementKey) ??
                  "success"
                }
              />
              {isTraveler && (
                <DropdownButton
                  variant="outlined"
                  color={travelerElement}
                  title={<strong>{travelerElement}</strong>}
                  sx={{ borderRadius: 4 }}
                >
                  {allTravelerElements.map((ele) => (
                    <MenuItem
                      key={ele}
                      selected={ele === travelerElement}
                      disabled={ele === travelerElement}
                      onClick={() => setTravelerElement(ele)}
                    >
                      <strong>
                        {ele.charAt(0).toUpperCase() + ele.slice(1)}
                      </strong>
                    </MenuItem>
                  ))}
                </DropdownButton>
              )}
            </Box>
            {!!onClose && (
              <CloseButton
                onClick={onClose}
                sx={{ borderRadius: 4 }}
                aria-label="close"
              />
            )}
          </Box>
          <Tabs
            value={buildTab}
            onChange={handleBuildChange}
            aria-label="build tabs"
            variant="fullWidth"
            TabIndicatorProps={{
              sx: {
                backgroundColor:
                  theme.palette[
                    (characterInfo.vision_key.toLowerCase() as ElementKey) ??
                      "primary"
                  ].main,
              },
            }}
          >
            {characterBuilds?.builds.map((build, index) => (
              <Tab
                key={index}
                label={build.name}
                {...a11yProps(index)}
                sx={{
                  color: `${
                    theme.palette[
                      (characterInfo.vision_key.toLowerCase() as ElementKey) ??
                        "primary"
                    ].main
                  } !important`,
                }}
              />
            ))}
          </Tabs>
        </CardContentEvenPadding>
      </Card>
      <div id="back-to-top-anchor" />
      <Suspense
        fallback={
          <Skeleton
            variant="rectangular"
            sx={{ width: "100%", height: "100%", minHeight: 5000 }}
          />
        }
      >
        <Card sx={{ borderRadius: 4 }}>
          <CardContentEvenPadding>
            <Grid container spacing={2} sx={{ justifyContent: "center" }}>
              <Grid item xs={8} sm={5} md={4} lg={2.3} height="auto">
                <CharacterCard
                  characterKey={characterKey}
                  element={characterInfo.vision_key.toLowerCase() as ElementKey}
                />
              </Grid>
              <Grid
                item
                xs={12}
                sm={7}
                md={8}
                lg={9.7}
                sx={{ display: "flex", flexDirection: "column" }}
              >
                {characterBuilds?.builds.map((build, index) => {
                  return (
                    <TabPanel key={index} value={buildTab} index={index}>
                      <BuildDisplay
                        build={build}
                        element={
                          characterInfo.vision_key.toLowerCase() as ElementKey
                        }
                        notes={characterBuilds.notes}
                      />
                    </TabPanel>
                  );
                })}
              </Grid>
            </Grid>
          </CardContentEvenPadding>
        </Card>
      </Suspense>
    </>
  );
};
const CharacterCard = ({
  characterKey,
  element = "pyro",
}: {
  characterKey: CharacterKey;
  element: ElementKey;
}) => {
  return (
    <Box
      src={
        characterKey === "Traveler"
          ? characterAssets["Traveler"].TravelerFemaleCard
          : characterAssets[characterKey].Card
      }
      component="img"
      width="100%"
      height="auto"
      sx={{
        borderRadius: 4,
        borderColor: theme.palette[element].main,
        borderWidth: "2px",
        borderStyle: "solid",
      }}
    />
  );
};

export default PageCharacter;
